import uniPopup from "@/components/uni-popup/uni-popup.vue"

import {mapState,mapGetters,mapActions,mapMutations} from "vuex"

export default{
	components:{
		uniPopup
	},
	props:{

	},
	data() {
		return {
			isNewAdd:false,
			typeName:'',
			typeIndex:'',
			isTouch:true
		}
	},
	computed:{
		...mapState({

		}),
		...mapGetters([
		]),

	},
	methods:{
		...mapMutations([
			
		]),
		...mapActions([
		]),
		nameInput(e){
			this.typeName = e.detail.value
		},
		indexInput(e){
			this.typeIndex = e.detail.value
		},
		ayncGoodsType(operation){
			if(this.typeName === ''){
				uni.showToast({
					title: '请输入正确的类别名',
					icon: 'none',
					duration:1000
				})
			}else if(this.typeIndex === ''){
				uni.showToast({
					title: '请输入正确的排序数字',
					icon: 'none',
					duration:1000
				})
			}else{
				if(this.isTouch){
					uni.showLoading({title: '同步中...'})
					this.isTouch = false
					return new Promise((res,rej)=>{
						this.$H.post('/api/ProductType/add',{
							Name:this.typeName,
							SortValue:this.typeIndex
						},{
							token:true
						}).then(res=>{
							console.log(res)
							uni.hideLoading()
							this.isTouch = true
							if(res.status === 0){
								
							}
						}).catch(err=>{
							uni.hideLoading()
							this.isTouch = true
						})
					})
				}
			}
		},
		change(e){
			if(!e.show){
				this.typeName = ''
				this.typeIndex = ''
			}
		},
		showAddTypePopup(isNewAdd){
			this.isNewAdd = isNewAdd
			this.$refs.popup.open()
		}
	}	
}
